.z-popover{
    @apply z-[9999]
}
.z-menu{
    @apply z-[9998]
}
.z-body{
    @apply z-[9997]
}
.z-bottom{
    @apply z-[9996]
}
.light-shadow{
    @apply shadow-[inset_0px_1px_1px_0px_rgb(0,0,0,0.25)]
}
.dark-shadow{
    @apply dark:shadow-[4px_4px_4px_0px_rgb(255,255,255,0.1),inset_1px_1px_1px_0px_rgb(255,255,255,0.1)]
}
.card-light-shadow{
    @apply shadow-[4px_4px_4px_0px_rgb(0,0,0,0.25),inset_1px_1px_1px_0px_rgb(0,0,0,0.25)]
}
.kanji-card-shadow{
    @apply shadow-[0px_4px_4px_0px_rgb(0,0,0,0.25),inset_1px_1px_1px_0px_rgb(0,0,0,0.25)]
}
.dark-kanji-card-shadow{
    @apply dark:shadow-[0px_4px_4px_0px_rgb(255,255,255,0.1),inset_1px_1px_1px_0px_rgb(255,255,255,0.1)]
}
*:focus {
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}
.btn{
    cursor:pointer;
    section:none;
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    /* -webkit-tap-highlight-color: rgba(0,0,0,0); */
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}