  /* .rhap_progress-section .rhap_progress-container {
    width: 80%;
  } */
  .rhap_controls-section{
    height: 0.8em;
    padding: 0;
    margin: 0;
  }
  .rhap_theme-color{
      color:'red'
  }
  .rhap_progress-filled {
    height: 7px;
    background-color:#F59815;
  }
  .rhap_progress-indicator {
    /* width: 10px; */
    /* height: 10px; */
    background-color:#F59815;
  }
  .rhap_download-progress {    
    background-color:#B3B3B3;
  }
  .rhap_progress-container {
    /* background-color: red; */
    /* height: 10px; */
    outline:none;
    padding: 0;
  }
  .rhap_time {
    color: #424242;
    font-size: #424242;
    font-weight: 500;
  }
  /* Main Control */
  .rhap_play-pause-button>svg, .rhap_rewind-button>svg, .rhap_forward-button>svg {
    width: 0.8em;
    height: 0.8em;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
    outline: none;
    section:none;
    color: #424242;
  }
  .rhap_play-pause-button{
    outline:none
  }
  .rhap_container{
    /* background:red; */
    outline:none;
    box-shadow: 4px 4px 4px 0px rgba(0, 0, 0, 0.25), inset 1px 1px 1px 0px rgba(0, 0, 0, 0.25),;

  }
  .rhap_controls-section{
    /* background:blue; */
    margin:5px 0px 0px 0px;
  }

.rhap_additional-controls{
  margin:0px 0px 0px 15px;
